import { InjectionToken } from '@angular/core';
import { Observable } from 'rxjs';
import { LOOKUP_HTTP_PARAMS } from './RemoteParams';
import { LookupGridResult } from '../lookup-grid-options';

export interface ILookupHttpService {
    context?: any;
    /**
     * ID
     * @param uri ID or Rest API
     * @param data  key/value
     */
    getData(uri: string, params?: LOOKUP_HTTP_PARAMS): Observable<LookupGridResult>;
}
export const ServerSideToken = new InjectionToken<ILookupHttpService>('Lookup Grid HTTP service');
