import { DataFormatter } from '@farris/ui-common/column';

/**  */
export const FAVORITE_FIELD_NAME = '__favorite__';
export const INPUT_GROUP_ICON = '<i class="f-icon f-icon-lookup"></i>';

// 
export type ComponentInstanceType = 'datatable'| 'treetable' | 'fav' | 'selected' | 'leftTree' | 'leftDataTable';

export enum LookupGridDisplayType {
    List = 'LIST',
    TreeList = 'TREELIST',
    NavList = 'NAVLIST',
    NavTreeList = 'NAVTREELIST',
    NavListTree = 'NAVLISTTREE'
}

/**  */
export enum FavoriteIcon {
    /**  */
    yes = '<span class="f-icon f-icon-star f-lookup-favorite" style="font-size:12px" ></span>',
    /**  */
    no = '<span class="f-icon f-icon-star-outline f-lookup-favorite" style="font-size:12px"></span>',
    /**  */
    delete = '<span class="f-icon f-icon-apply-format f-lookup-unfavorite" style="font-size:12px"></span>',
    /**  */
    remove = '<span class="f-icon f-icon-apply-format f-lookup-unfavorite" style="font-size:12px"></span>'
}

/**  */
export enum FavoriteAction {
    /**  */
    add = 'append item to favorite.',
    /**  */
    delete = 'remove favorite.'
}

export interface ExtInfoFormatterParam {
    [key: string]: any;
    bindingData?: any;
    instance?: any;
}

export type ExtInfoFormatterFn = (param: ExtInfoFormatterParam) => string;
export type ExtInfoFormatter = string | ExtInfoFormatterFn;

export interface CustomDataFormatter {
    /**  */
    [fieldName: string]: DataFormatter ;
    /**  */
    rowStyler?: (e: { data: any }) => {[key: string]: any};
    /**  */
    cellStyler?: (e: { data: any }, field: string) => {[key: string]: any};
}

export interface QuickSelectOption {
    /**  */
    enable?: boolean;
    /** top  10 */
    showItemsCount?: number;
    /**  */
    textField?: string;
    /**  */
    formatter?: (data: any) => string;
    
    footerHeight?: number;
    /**   */
    showMore?: boolean;
    /** ,  200px */
    minWidth?: number;
    /**  false */
    enableBackspace?: boolean;
}

export const QuickSelectDefaultOptions: QuickSelectOption = {
    enable: false,
    showMore: true,
    showItemsCount: 10,
    footerHeight: 0,
    minWidth: 200,
    enableBackspace: false
}