import { DataColumn } from '@farris/ui-common/column';

export type ONLY_SELECT_LEAF = 'default' | 'yes' | 'no';
export type LOAD_DATA_TYPE = 'all' | 'fav' | 'list' | 'nav' | 'search' | 'children'
                            | 'textchange' | 'sort' | 'navsearch' | 'selected' | 'allChildren' | 'navAllChildren';


export enum LookupLayout {
    /**  */
    default = 'default',
    /**  */
    advanced = 'advanced'
}
export interface TreeInfo {
    /**  all, async */
    loadDataType?: 'all' | 'async';
    /**  pathcode, parentId */
    layerType?: string;
    /**  */
    dataField?: string;
    /** ID */
    parentField?: string;
    /**  */
    pathField?: string;
    /**  */
    layerField?: string;
    /**  */
    isDetailField?: string;
    /**
     * 
     * - default:   `no`
     * - `yes`: 
     * - `no`:  
     */
    onlySelectLeaf?: boolean | ONLY_SELECT_LEAF;
    /**  */
    treeDataIsInit?: boolean;
}

export interface CustomStyleData {
    field: string, 
    value: string, comparator?: string, 
    styles: { [key: string]: any }
}

export interface LookupGridResult {
    /**  */
    columns?: DataColumn[];
    /**  */
    items?: any[];
    /**  */
    searchFields?: { label: string, value: string }[];
    /**  */
    pageInfo?: { pageIndex: number, pageSize: number, pageList?: number[], enablePager: boolean };
    /**  */
    total?: number;
    /**  */
    mapFields?: { desgine: string, runtime: string }[];
    /**  */
    treeInfo?: TreeInfo;
    displayType?: string;
    displayInfo?: DisplayInfo;
    title?: string;
    navigation?: NavigationOptions;
    valueField?: string;
    textField?: string;
    idField?: string;
    /**  */
    selectedItems?: any;
    /**  */
    favoriteItems?: any[];
    /**  */
    customStyles?: {
        row?: Array<CustomStyleData>,
        cell?:Array<CustomStyleData>
    }
}

/**  */
export interface PersonalConfig {
    tabIndex?: string;
    favorite?: {
        [lang: string]: any[];
    };
    quickSelected?: {
        [lang: string]: any[];
    };
    size?: any;
    selections?: any;
    cascadeStatus?: string;
    [key: string]: any;
}

export interface NavigationOptions extends LookupGridResult {
    [key: string]: any;
    displayType: 'List' | 'TreeList';
    width: number;
    idField?: string;
    relations?: { groupField: string, helpField: string }[];
    hover?: boolean;
}

export interface LookupGridOptions extends LookupGridResult {
    singleSelect?: boolean;
    idField?: string;
    uri?: string; // IDwebapi 
    showFilterBar?: boolean;
    pagination?: boolean;
    pageIndex?: number;
    pageSize?: number;
    pageList?: number[];
    total?: number;
    width?: number;
    height?: number;
    hover?: boolean;
}

export class DisplayInfo {
    title?: string;
    favorites?: string;
    okText?: string;
    cancelText?: string;
    allColumns?: string;
}

export const lookupGridDefaults: LookupGridOptions = {
    singleSelect: true,
    showFilterBar: true,
    pagination: true,
    pageIndex: 1,
    pageSize: 10,
    pageList: [10, 20, 30, 50, 100]
};

export const displayInfoDefault = {
    title: '',
    favorites: '',
    okText: '',
    cancelText: '',
    allColumns: ''
};

/**  */
export interface PickingResult {
    /** true: false:  */
    showDialog: boolean;
    /**  */
    data?: any;
    message?: any;
}

export interface PickedResult {
    closeDialog: boolean;
    message?: any;
}
